<template>

    <div v-if="cartEmpty() === 0">
        <h1>The Cart is Empty. <i class="fas fa-frown"></i></h1>
    </div>
    <div v-else>
    <div class="wrapper">
        <div class="products">
                <div class="cart" v-for="product in cart" :key="product.id">
                    <div class="info">
                        <h1>{{product.name}}</h1>
                        <p>{{product.country}}</p>
                    </div>
                    <div class="image">
                        <img :src="'/images/products/'+product.image">
                    </div>
                    <div class="price">
                        <h2>{{product.price}}</h2>
                        <button class="auto" @click="clearFromCart(product)">Clear from Cart</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'CartList',
        props:{
            cart: Array
        },

        methods:{
            clearFromCart(product){
                let index = this.$root.$data.cart.indexOf(product);
                this.$root.$data.cart.splice(index, 1);
            },
            cartEmpty(){
                return this.$root.$data.cart.length;
            }
        }
    }
</script>

